.header-wrapper {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  margin-bottom: 8px;
  margin-top: 16px;
}

.go-back-wrapper {
  display: flex;
  justify-content: center;
  flex: 1;
}

.menu-wrapper {
  flex: 1;
}

.header-logo-wrapper {
  display: flex;
  flex: 3;
  justify-content: center;
}



.menu-wrapper {
  text-align: center;
}

@media (max-width: 800px)
{
  .header-logo {
    max-height: 100%;
    max-width: 100%;
  }

}
